// Men√∫  //
.menu-top {
     border-bottom: 1px solid $color-brand;
     .logo-yaguar {
          max-height: 51px;
          width: auto;
          @media (max-width: 991px) {
               max-height: 40px;
               padding: 5px;
          }
     }
     .nav-link {
          font-family: $font-family;
          font-size: 16px;
          font-weight: 500;
     }
     &-collapse {
          &.navbar-collapse {
               flex-grow: unset;
          }
     }
     .collapse-hidden {
          @media (max-width: 991px) {
               &_pc {
                    display: flex !important;
               }
               &_movil {
                    display: none !important;
               }
          }
          @media (min-width: 992px) {
               &_pc {
                    display: none !important;
               }
               &_movil {
                    display: flex !important;
               }
          }
     }
}

@media (min-width: 320px) and (max-width: 991px) {
     .menu-top {
          border-bottom: 1px solid #ff4419;
          width: 100%;
          background: white;
          z-index: 13;
     }
     .grupo-botones {
          position: absolute;
          bottom: 0px;
          display: flex;
          flex-direction: column;
          align-items: center;
          width: 100%;
     }
     .menu-top-collapse {
          visibility: hidden;
          .navbar-nav {
               width: 100%;
               .nav-item {
                    border-bottom: 1px solid #60606066;
                    width: 100%;
                    .nav-link {
                         margin: 10px 0;
                         font-family: "Gilroy", sans-serif;
                         font-size: 18px;
                         font-weight: 500;
                    }
               }
          }
     }
     .menu-top.open {
          align-content: flex-start;
          border-bottom: 1px solid #ff4419;
          position: sticky;
          top: 0px;
          background-color: white;
          z-index: 1;
          display: flex;
          .menu-top-collapse {
               display: flex;
               flex-direction: column;
               height: calc(90vh - 50px);
               justify-content: space-between;
               visibility: visible;
               transition-delay: 50ms;
          }
     }
}

header {
     .py-header {
          padding: 100px 0;
          @media (min-width: 320px) and (max-width: 767px) {
               padding: 80px 0 0px 0;
          }
          @media (min-width: 768px) and (max-width: 991px) {
               padding: 80px 0 40px 0;
          }
     }
     .header {
          &-titulo {
               font-size: 60px;
