//Pasos
.pasos {
     &-pc {
          @media (min-width: 320px) and (max-width: 767px) {
               display: none;
          }
          @media (min-width: 768px) {
               display: flex;
               align-items: start;
          }
          margin: 80px 0 0px 0;
          &-menu {
               &-title {
                    margin-bottom: 36px;
                    font-size: 34px;
                    font-weight: bold;
               }
               &-btn {
                    display: inline-flex;
                    justify-content: space-between;
                    align-items: center;
                    margin-bottom: 26px;
                    border: none;
                    padding: 18px 20px;
                    border-radius: 4px;
                    background-color: white;
                    box-shadow: 2px 2px 14px -10px #00000063;
                    span {
                         color: $color-brand-2;
                         font-size: 20px;
                         font-weight: 500;
                         font-family: $font-family;
                    }
                    &.active {
                         &:nth-child(odd) {
                              box-shadow: 2px 2px 14px -10px #00000063;
                              background-color: $color-brand;
                              span {
                                   color: white;
                              }
                              img {
                                   filter: invert(1);
                              }
                         }
                    }
                    &.active {
                         &:nth-child(even) {
                              box-shadow: 2px 2px 14px -10px #00000063;
                              background-color: $color-brand-2;
                              span {
                                   color: white;
                              }
                              img {
                                   filter: invert(1);
                              }
                         }
                    }
               }
               .pasos-pc-menu-text {
                    &-0,
                    &-1,
                    &-2,
                    &-3,
                    &-4 {
                         display: none;
                         .active {
                              display: block;
                         }
                    }
               }
               .pasos-pc-menu-text {
                    &-0.active,
                    &-1.active,
                    &-2.active,
                    &-3.active,
                    &-4.active {
                         display: block !important;
                    }
               }
          }

          &-contenido {
               display: flex;
               justify-content: center;
               align-items: center;

               &-item {
                    .pasos-img {
                         max-height: 640px;
                         width: auto;
                    }
               }
          }
          @media (min-width: 320px) and (max-width: 991px) {
               &-menu {
                    &-title {
                         margin-bottom: 36px;
                         font-size: 30px;
                         font-weight: bold;
                    }
                    &-btn {
                         display: inline-flex;
                         justify-content: space-between;
                         align-items: center;
                         margin-bottom: 20px;
                         border: none;
                         padding: 12px 14px;
                         border-radius: 4px;
                         background-color: white;
                         box-shadow: 2px 2px 14px -10px #00000063;
                         span {
                              color: $color-brand-2;
                              font-size: 18px;
                              font-weight: 500;
                              font-family: $font-family;
                         }
                         &.active {
                              &:nth-child(odd) {
                                   box-shadow: 2px 2px 14px -10px #00000063;
                                   background-color: $color-brand;
                                   span {
                                        color: white;
                                   }
                                   img {
                                        filter: invert(1);
                                   }
                              }
                         }
                         &.active {
                              &:nth-child(even) {
                                   box-shadow: 2px 2px 14px -10px #00000063;
                                   background-color: $color-brand-2;
                                   span {
                                        color: white;
                                   }
                                   img {
                                        filter: invert(1);
                                   }
                              }
                         }
                    }
               }

               &-contenido {
                    display: flex;
                    justify-content: center;
                    align-items: center;

                    &-item {
                         .pasos-img {
                              max-height: 450px;
                              width: auto;
                         }
                    }
               }
          }
     }
     &-movil {
          margin: 60px 0 0px 0;
          &-menu {
               &-title {
                    margin-bottom: 36px;
                    font-size: 30px;
                    font-weight: bold;
                    text-align: center;
               }
          }
          @media (min-width: 320px) and (max-width: 767px) {
               display: block;
          }
          @media (min-width: 768px) {
               display: none;
          }

          .pasos-img {
               max-width: 100%;
          }
          .accordion-item {
               border: none;
          }
          .accordion-button {
               font-size: 18px;
               font-weight: 500;
               background-color: white;
               color: $color-brand-2;
               border-radius: 6px;
               border-radius: 6px;
               box-shadow: 3px 0px 18px -12px rgb(0 0 0 / 52%);
               margin: 20px 0;
          }
          .accordion-button:focus,
          .accordion-button:focus-visible {
               box-shadow: 3px 0px 18px -12px rgb(0 0 0 / 52%);
          }
          .accordion-button:not(.collapsed) {
               background-color: white;
               color: $color-brand-2;
               border-radius: 6px;
               border-radius: 6px;
               box-shadow: 3px 0px 18px -12px rgb(0 0 0 / 52%);
          }
          .accordion-body {
               display: flex;
               justify-content: center;
               flex-direction: column;
               font-size: 18px;
          }
     }
}
//Testimonios
.testimonios {
     padding: 50px 0px 50px 0px;
     .carousel_testimonios {
          width: 100%;
          margin: 0px auto;
     }
     .slick-slide {
          margin: 15px;
     }
     .slick-track {
          display: flex;
          align-items: center;
     }
     .card {
          border: none;
          border-radius: 6px;
          box-shadow: 3px 8px 15px -2px #0000000f;
          background: #f8f9fa;
     }
     .card-body {
          padding: 30px;
          text-align: justify;
     }
     .card-text {
          padding: 30px 0px;
     }
     .card-footer {
          &_cliente {
               display: flex;
               flex-direction: row;
               align-items: center;
               .card-cliente {
                    margin: 0 15px;
               }
               .card-avatar {
                    height: 55px;
                    width: auto;
                    border-radius: 6px;
                    img {
                         width: 100%;
                         padding: 0px;
                    }
               }
               .card-nombre {
                    font-size: 20px;
               }
               .card-profesion {
                    color: $color-brand;
                    font-size: 16px;
               }
          }
     }
     @media (min-width: 320px) and (max-width: 991px) {
          .card {
               margin: 0px;
          }
     }
}

//Planes
.planes {
     .separador {
          @media (min-width: 320px) and (max-width: 767px) {
               &_v {
                    display: none;
               }
               &_h {
                    width: auto;
                    height: auto;
                    display: flex;
                    justify-content: center;
                    margin: 80px 0;
                    width: 100%;
               }
          }
          @media (min-width: 768px) {
               &_h {
                    display: none;
               }
               &_v {
                    width: auto;
                    height: auto;
                    display: flex;
                    justify-content: center;
               }
          }
     }

     .row {
          border-radius: 10px;
     }
     .plan {
          display: flex;
          flex-direction: column;
          justify-content: space-around;
          li {
               font-weight: 400;
               margin-bottom: 12px;
               font-size: 20px;
          }
          li.check {
               list-style-image: url(/asset/imagenes/icons/mdi_check-bold.png);
          }
          li.uncheck {
               list-style-image: url(/asset/imagenes/icons/uncheck-bold.png);
          }
          &-titulo {
               text-align: center;
               font-size: 40px;
               font-weight: bold;
               margin-bottom: 36px;
          }
          &-precios {
               text-align: center;
               font-weight: bold;
               font-size: 40px;
          }
          &-footer {
               text-align: center;
               .precio {
                    &-oferta {
                         font-size: 40px;
                         text-align: center;
                         font-weight: bold;
                         span {
                              font-size: 18px;
                              font-weight: 500;
                         }
                    }
                    &-normal {
                         font-size: 20px;
                         text-align: center;
                         font-weight: bold;
                         text-decoration: line-through;
                         text-decoration-color: red;
                         span {
                              font-weight: 500;
                         }
                    }
               }
          }
     }
}

.container-empezar {
     display: grid;
     grid-template-columns: repeat(4, 1fr);
     grid-template-rows: repeat(3, auto);
     border-radius: 10px;
     overflow: hidden;
     padding: 100px 0px 0px 0px;
     &_cta {
          grid-area: 2/1/3/3;
          z-index: 2;
          margin-left: 50px;
     }
     &_logo {
          height: 34px;
     }
     &_description {
          font-size: 60px;
          font-weight: bold;
          line-height: 70px;
          margin-bottom: 34px;
     }
     @media (max-width: 991px) {
          &_logo {
               height: 34px;
               left: 50%;
               position: relative;
               transform: translateX(-50%);
          }
          &_description {
               font-size: 44px;
               font-weight: bold;
               line-height: 55px;
               margin-bottom: 34px;
               text-align: center;
               width: 70%;
               left: 50%;
               position: absolute;
               transform: translateX(-50%);
          }
          &_cta {
               grid-area: 1/1/4/5;
               z-index: 2;
               position: relative;
               margin-left: unset;
               .btn_naranja {
                    position: absolute;
                    bottom: 10px;
                    width: 90%;
                    left: 50%;
                    position: absolute;
                    transform: translateX(-50%);
               }
          }
     }
     &_img {
          z-index: 0;
          grid-area: 1/1/4/5;
          justify-self: end;
          border-radius: 0 10px 10px 0;
          &_pc {
               grid-area: 1 / 1 / 4 / 5;
               height: 100%;
               display: block;
               justify-self: end;
               border-radius: 0 10px 10px 0;
               @media (max-width: 991px) {
                    display: none;
               }
          }
          &_movil {
               grid-area: 1 / 1 / 4 / 5;
               justify-self: end;
               height: 100%;
               z-index: 0;
               border-radius: 0 10px 10px 0;
               display: none;
               @media (max-width: 991px) {
                    display: block;
                    width: 100%;
                    height: auto;
                    align-self: end;
                    justify-self: stretch;
               }
               @media (min-width: 768px) and (max-width: 991px) {
                    display: block;
                    width: 100%;
                    height: auto;
                    align-self: end;
                    justify-self: stretch;
               }
               @media (max-width: 992px) and (max-width: 1199px) {
                    display: block;
                    width: 100%;
                    height: auto;
                    align-self: end;
                    justify-self: stretch;
               }
          }
     }
     &_overlay {
          z-index: 1;
          grid-area: 1/1/4/4;
          background: linear-gradient(90deg, white 40%, #fff0);
          @media (max-width: 991px) {
               grid-area: 1/1/4/5;
               background: linear-gradient(
                    180deg,
                    rgba(255, 255, 255, 0.84) 5%,
                    rgba(255, 255, 255, 0.0392156863) 34%,
                    rgba(255, 255, 255, 0)
               );
          }
     }
}

//CTA
.cta_ws {
     height: 300px;
     span {
          font-weight: 500;
     }
}

//Footer
footer {
     width: 100%;
     border-top: 1px solid $color-brand;
     padding-bottom: 20px;
     .footer {
          display: flex;
          justify-content: space-between;
          flex-direction: row;
          ul {
               display: flex;
               flex-direction: row;
               padding-left: 0px;
          }
          li:not(:last-child) {
               margin-right: 15px;
          }
          li {
               list-style: none;
               font-size: 14px;
               font-weight: 400;
               letter-spacing: 0.05em;
               img {
                    margin-right: 5px;
                    height: 20px;
               }
          }
     }
     @media (max-width: 991px) {
          .footer {
               flex-direction: column;
               padding: 25px 33px;
               ul {
                    display: flex;
                    flex-direction: row;
                    padding-left: 0px;
               }
               li {
                    list-style: none;
               }
               .lista_left {
                    flex-direction: column;
                    li {
                         font-size: 16px;
                         margin-bottom: 8px;
                    }
               }
          }
     }
}
